<template>
    <v-container fluid fill-height>
        <v-layout align-center justify-center>
        <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
                <v-toolbar dark color="grey">
                    <v-spacer></v-spacer>
                    <v-toolbar-title><span class="">تسجيل دخول  مستخدمين</span></v-toolbar-title>
                    <v-spacer></v-spacer>
                </v-toolbar>
                <v-card-text>
                    <v-layout row wrap>
                        <v-flex xs12>
                            <SocialLoginIcons />
                        </v-flex>
                        <v-flex xs12>
                            <v-form class="ma-4">
                                <v-text-field v-model="email" prepend-icon="phone" name="login" placeholder="رقم الجوال" type="text"></v-text-field>
                                <v-text-field v-model="password" id="password" prepend-icon="lock" name="password" placeholder="كلمه المرور" type="password"></v-text-field>
                            </v-form>
                        </v-flex>
                    </v-layout>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="success" @click="submit"><span class="cairo">تسجيل دخول</span></v-btn>
                    <v-spacer></v-spacer>
                    <v-btn flat @click="resetPassword" color="primary"><span class="cairo">نسيه كلمه المرور</span></v-btn>
                    <v-spacer></v-spacer>
                </v-card-actions>
            </v-card>
        </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
  import AuthenticationServec from '../../services/AuthenticationServec'
  import SocialLoginIcons from '../../components/SocialLoginIcons'
  export default {
    name: 'UserLogin',
    components: {
        SocialLoginIcons
    },
    data(){
        return {
            email: '',
            password: '',
            error: ''
        }
    },
    methods: {
        async submit(){
            try {
                await AuthenticationServec.login({
                    email: this.email,
                    password: this.password
                })
                this.$router.push('/profile/user')
            } catch (error) {
                this.error = error.response.data.error
            }
        },
        resetPassword(){
            this.$router.push('/login/reset')
        }
    }
  }
</script>